<template>
    <div>
        <div v-if="change_pwd == 1" class="setPwd">
            <div class="title bold">
                修改密码
            </div>
            <div class="main ac">
                <div class="pwdType">
                    <a @click="type=1" :class="['bold',{selec:type ===1}]">原始密码修改</a>
                    <a @click="type=2" :class="['bold',{selec:type ===2}]">短信验证修改</a>
                </div>
                <div class="form ac" v-if="type===1">
                    <table>
                        <tr>
                            <td class="form-title"><p>原始密码：<span></span></p></td>
                            <td class="input"><input type="password" v-model="OldPassword"></td>
                        </tr>
                        <tr>
                            <td class="form-title"><p>新密码：<span></span></p></td>
                            <td class="input"><input type="password" v-model="NewPassword"><span class="al notice">请设置8-20位包含数字、大小写字母的密码</span>
                            </td>
                        </tr>
                        <tr>
                            <td class="form-title"><p>确认密码：<span></span></p></td>
                            <td class="input"><input type="password" v-model="NewPasswordVerify" @blur="CheckPassword"><span
                                    class="al notice red--text" v-show="ShowNotice">{{PasswordNotice}}</span></td>
                        </tr>
                        <tr>
                            <td class="form-title">
                                &nbsp;&nbsp;
                            </td>
                            <td class="input">
                                <div class="changePwd cursor bold" @click="ChangePwd">
                                    确认
                                </div>
                            </td>
                        </tr>
                    </table>
                </div>
                <div class="form ac" v-if="type===2">
                    <table>
                        <tr>
                            <td class="form-title"><p>手机号码：<span></span></p></td>
                            <td class="input"><p class="al">{{ phone }}</p></td>
                        </tr>
                        <tr>
                            <td class="form-title"><p>验证码：<span></span></p></td>
                            <td class="input">
                                <input type="text" v-model="VerifyCode">
                                <div class="verify-code cursor">
                                    <Coder :phone="phone"></Coder>
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <td class="form-title"><p>新密码：<span></span></p></td>
                            <td class="input"><input type="password" v-model="NewPassword"><span class="al notice">请设置8-20位包含数字、大小写字母的密码</span>
                            </td>
                        </tr>
                        <tr>
                            <td class="form-title"><p>确认密码：<span></span></p></td>
                            <td class="input"><input type="password" v-model="NewPasswordVerify" @blur="CheckPassword"><span
                                    class="al notice red--text" v-show="ShowNotice">{{PasswordNotice}}</span></td>
                        </tr>
                        <tr>
                            <td class="form-title">
                                &nbsp;&nbsp;
                            </td>
                            <td class="input">
                                <div class="changePwd cursor bold" @click="ChangePwdBySms">
                                    确认
                                </div>
                            </td>
                        </tr>
                    </table>
                </div>
            </div>
        </div>
        <div v-if="change_pwd == 0" class="setPwd">
            <div class="title bold">
                设置密码
            </div>
            <div class="main ac">
                <div class="pwdType">

                </div>
                <div class="form ac">
                    <table>
                        <tr>
                            <td class="form-title"><p>新密码：<span></span></p></td>
                            <td class="input"><input type="password" v-model="NewPassword"><span class="al notice">请设置8-20位包含数字、大小写字母的密码</span>
                            </td>
                        </tr>
                        <tr>
                            <td class="form-title"><p>确认密码：<span></span></p></td>
                            <td class="input"><input type="password" v-model="NewPasswordVerify" @blur="CheckPassword"><span
                                    class="al notice red--text" v-show="ShowNotice">{{PasswordNotice}}</span></td>
                        </tr>
                        <tr>
                            <td class="form-title">
                                &nbsp;&nbsp;
                            </td>
                            <td class="input">
                                <div class="changePwd cursor bold" @click="SetPwd">
                                    确认
                                </div>
                            </td>
                        </tr>
                    </table>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Coder from '../../../../components/VerifyCode'
    let display = {
        showRecommend: false,
        nav: 'default',
        header: 'default'
    };
    export default {
        name: 'setpwd',
        metaInfo: {
            title: '修改密码',
            meta: [
                {name: 'keywords', content: '福分通'},
                {name: 'description', content: '专注企业员工福利'}
            ]
        },
        data() {
            return {
                change_pwd:this.storage.getStorage('FFT').change_pwd || 0,
                type: 1,
                alert: true,
                PasswordNotice: '请确认两次密码的正确性！',
                ShowNotice: false,
                checked: false,//验证密码有效性，无效则无法确认
                OldPassword: '',
                NewPassword: '',
                NewPasswordVerify: '',
                VerifyCode: '',
                phone: this.storage.getStorage('FFT').mobile,
            }
        },
        async created() {
            this.$store.dispatch('layout/Change', display);
        },
        methods: {
            /*
            * 输入框失去焦点确认一下密码是否符合规范
            * */
            CheckPassword() {
                //判断密码长度，密码是否包含数字和字符。
                let reg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,20}$/;
                if (this.NewPasswordVerify.replace(/ /g, '') === '') {
                    this.ShowNotice = false;
                    this.NewPassword = '';
                    this.NewPasswordVerify = '';//当确认密码框全是空格的时候会清空输入框
                    this.checked = false;
                    return
                }
                //匹配密码是否符合规范
                if (!reg.test(this.NewPasswordVerify)) {
                    this.PasswordNotice = '密码不符合规范！';
                    this.ShowNotice = true;
                    this.checked = false;
                    return
                }
                //匹配两次密码是否统一
                if (this.NewPasswordVerify === this.NewPassword) {
                    this.ShowNotice = false;
                    this.checked = true
                } else {
                    this.ShowNotice = true;
                    this.checked = false;
                    this.PasswordNotice = '两次输入密码不符，请检查是否输入错误！';
                }
            },
            SetPwd(){
                let data = {
                    old_pwd:'12345678',
                    new_pwd: this.NewPasswordVerify,
                    mobile: this.phone
                };
                let reg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,20}$/;
                if (!reg.test(data.new_pwd)){
                    this.bus.$emit('notice', '请确认密码的有效性！');
                    return
                }
                if (this.NewPasswordVerify === this.NewPassword) {
                    this.ShowNotice = false;
                    this.checked = true
                } else {
                    this.ShowNotice = true;
                    this.checked = false;
                    this.PasswordNotice = '两次输入密码不符，请检查是否输入错误！'
                }
                if (this.checked===false) {
                    this.bus.$emit('notice', '请确认密码的有效性！');
                    return
                }
                this.yum('api/member/changePwd', data).then(res => {
                    if (res.data.status === 200) {
                        this.bus.$emit('notice', res.data.msg);
                        setTimeout(()=>{
                            this.ClearLogin()
                        },1100)
                    } else {
                        this.bus.$emit('notice', res.data.msg)
                    }
                })
            },
            ChangePwd() {
                let data = {
                    old_pwd: this.OldPassword,
                    new_pwd: this.NewPasswordVerify,
                    mobile: this.phone
                };
                let reg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,20}$/;
                if (!reg.test(data.new_pwd)){
                    this.bus.$emit('notice', '请确认密码的有效性！');
                    return
                }
                if (this.NewPasswordVerify === this.NewPassword) {
                    this.ShowNotice = false;
                    this.checked = true
                } else {
                    this.ShowNotice = true;
                    this.checked = false;
                    this.PasswordNotice = '两次输入密码不符，请检查是否输入错误！'
                }
                if (!this.checked) {
                    this.bus.$emit('notice', '请确认密码的有效性！');
                    return
                }
                this.yum('api/member/changePwd', data).then(res => {
                    if (res.data.status === 200) {
                        this.bus.$emit('notice', res.data.msg);
                        setTimeout(()=>{
                            this.ClearLogin()
                        },1100)
                    } else {
                        this.bus.$emit('notice', res.data.msg)
                    }
                })
            },
            ChangePwdBySms() {
                let data = {
                    verify_code: this.VerifyCode,
                    new_pwd: this.NewPasswordVerify,
                    mobile: this.phone
                };
                let reg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,20}$/;
                if (!reg.test(data.new_pwd)){
                    this.bus.$emit('notice', '请确认密码的有效性！');
                    return
                }
                if (this.NewPasswordVerify === this.NewPassword) {
                    this.ShowNotice = false;
                    this.checked = true
                } else {
                    this.ShowNotice = true;
                    this.checked = false;
                    this.PasswordNotice = '两次输入密码不符，请检查是否输入错误！'
                }
                if (!this.checked){
                    this.bus.$emit('notice', '请确认密码的有效性！');
                    return
                }
                this.yum('api/member/changePwdByPhone', data).then(res => {
                    if (res.data.status === 200) {
                        this.bus.$emit('notice', res.data.msg);
                        setTimeout(()=>{
                            this.ClearLogin()
                        },1100)
                    } else {
                        this.bus.$emit('notice', res.data.msg)
                    }
                })
            },
            ClearLogin(){
                this.storage.removeStorage('FFT');
                this.storage.removeStorage('business_data');
                this.storage.removeStorage('exchange');
                this.storage.removeStorage('fft-order-nums');
                this.storage.removeStorage('fft-user-info');
                location.assign('/user/clogin.html')
            }
        },
        components: {
            Coder
        },
        watch:{
            type:{
                handler(){
                    this.NewPassword = ''
                    this.NewPasswordVerify = ''
                },
                deep:true
            }
        }
    };
</script>

<style lang="stylus" scoped>
    .setPwd
        position relative
        min-height 300px
        padding-bottom 1px
        background-color white
        box-shadow 0 0 8px #bbb
        .title
            height 50px
            line-height 50px
            border-bottom 1px solid #ccc
            font-size 16px
            padding 0 10px
            margin 0 10px

    .pwdType
        margin-top 20px
        a
            font-size 18px
            height: 40px;
            display: inline-block;
            line-height: 40px;
            margin: 0 25px;
            &.selec
                border-bottom 2px solid
                color #ff0077

    .form table
        margin 0px auto 30px
        width 380px
        tr
            display block
            margin-top 20px
            height 40px
            line-height 40px
        td.form-title
            position relative
            width 80px
            color black
            p
                text-align justify
                height 40px
                span
                    width 100%
                    display inline-block
        td.input
            width 300px
            position relative
            input
                width 300px
                height 40px
                outline none
                padding 5px
                border-radius 4px
                border-style solid
                border-width 1px
                border-color #ccc
                &:focus
                    border-color #66ccff
            .notice
                position absolute
                width 300px
                height 15px
                line-height 15px
                left 0
                font-size 12px
                bottom -17px
                &.red--text
                    color red
            .changePwd
                width 100px
                color white
                font-size 16px
                background-color #ff0077
                border-radius 4px
            .verify-code
                position absolute
                width 100px
                height 100%
                top 0
                right 0
                background-color #dcdcdc
                border 1px solid #ccc
                border-radius 0 4px 4px 0
</style>
